<script>
  import { isActive, url } from "@sveltech/routify";

  let links = [];
  $: links = [
    ["./index", "Home"], //add index if you don't want siblings to be considered children
    ["./skills", "Skills"],
    ["./about", "About"],
    ["./lists", "Lists"],
    ["./projects", "Projects"]
  ].map(([path, name]) => {
    return {
      name,
      href: $url(path),
      active: $isActive(path)
    };
  });
</script>

<style>
  .active {
    color: red;
  }
</style>

<ul>
  {#each links as { name, href, active }}
    <li>
      <a {href} class="web-{name}" class:active>/{name}</a>
    </li>
  {/each}

</ul>
<slot />
